<template>
  <nav class="Navbar">
          <template v-if="user.loggedIn">
            <div class="nav-item">{{user.data.displayName}}</div> <!--Show usename-->
            <li class="nav-item">
                <!--Log out link-->
              <a class="nav-link" @click.prevent="signOut">Logga ut <i class=" fa fa-sign-in"></i></a>
            </li>
          </template>
          <template v-else>
              <!--Log in form/home-->
            <div class ="navContainer">
                <h2>Välkommen till Trädgårdsbloggen</h2>
                <li class="nav-i">
                <router-link to="login" class="nav-link">Logga in <i class=" fa fa-sign-in"></i></router-link>
                </li>
                <li class="nav-i">
                <router-link to="register" class="nav-link">Registera konto  <i class=" fa fa-sign-in"></i></router-link>
                </li>
            </div>
          </template>
  </nav>
</template>

<script>
import { mapGetters } from "vuex";
import firebase from "firebase";
export default {
  computed: {
    ...mapGetters({
      user: "user"
    })
  },
  methods: {
    signOut() {
      firebase
        .auth()
        .signOut()
        .then(() => {
          this.$router.replace({
            name: "home"
          });
        });
    }
  }
};
</script>

<style scoped>
.navContainer {
  position: relative;
  max-width: 600px;
  margin: 0 auto;
  background-color: rgba(245, 245, 245, 0.849);
  padding: 10px 10px 30px 10px;
  margin-top: 100px;
}

.navContainer li {
    margin: 30px; 
}

.navContainer h2 {
    margin: 30px;
}
</style>