import Vue from "vue";
import Vuex from "vuex";

Vue.use(Vuex); //Vuex library

export default new Vuex.Store({ //Check if user is has been authenticated or not
  state: {
    user: {
      loggedIn: false,
      data: null
    }
  },
  getters: { //Accessing data stored in the state. 
    user(state){
      return state.user
    }
  },
  mutations: { //Make changes to our state.
    SET_LOGGED_IN(state, value) {
      state.user.loggedIn = value;
    },
    SET_USER(state, data) { //SET_USER makes changes to the data property on state.user.
      state.user.data = data;
    }
  },
  actions: { //Commit mutations
    fetchUser({ commit }, user) { //FetchUser action here to commit the SET_LOGGED_IN and SET_USERmutations we defined earlier.
      commit("SET_LOGGED_IN", user !== null);
      if (user) {
        commit("SET_USER", {
          displayName: user.displayName,
          email: user.email
        });
      } else {
        commit("SET_USER", null);
      }
    }
  }
});