<template>
  <div class="containerForm">
   
          <h2>Registera konto</h2>
            <div v-if="error" class="alert alert-danger">{{error}}</div>
            <form class="form" action="#" @submit.prevent="submit">
                <label for="name">Namn</label>
            
                  <input
                    id="name"
                    type="name"
                    class="form-control"
                    name="name"
                    value
                    required
                    autofocus
                    v-model="form.name"
                  />
             
                <label for="email">E-postadress</label>
              
                    <input
                        id="email"
                        type="email"
                        class="form-control"
                        name="email"
                        value
                        required
                        autofocus
                        v-model="form.email"
                        
                    />
              
                <label for="password">Lösenord</label>
             
                  <input
                    id="password"
                    type="password"
                    class="form-control"
                    name="password"
                    required
                    v-model="form.password"
                  />

                  <button type="submit" class="btnLog">Registrera konto</button>
            </form>
      </div>   
</template>


<script>
import firebase from "firebase";

export default {
  data() {
    return {
      form: {
        name: "",
        email: "",
        password: ""
      },
      error: null
    };
  },
  methods: {
    submit() {
      firebase
        .auth()
        .createUserWithEmailAndPassword(this.form.email, this.form.password)
        .then(data => {
          data.user
            .updateProfile({
              displayName: this.form.name
            })
            .then(() => {this.$router.replace({ name: "Dashboard" });});
        })
        .catch(err => {
          this.error = err.message;
        });
    }
  }
};
</script>
