import axios from 'axios'; //Make HTTP requests from node.js with Axios

const url = 'api/posts/';

//PostService to handle requests from backend 
class PostService {

    //Get post
    static getPosts() {
        return new Promise(async(resolve, reject) => { //Returning data
            try {
                const res = await axios.get(url);
                const data = res.data;
                resolve (
                    data.map(post =>({ //High order function, loop data
                        ...post, //Spread operator
                        createdAt: new Date(post.createdAt), 
                    }))
                );
                }catch(err) {  
                 reject(err);             
            }
        })
    }

    //Create post
    static insertPost(text, author, pic, tArea) {
        return axios.post(url, {
            text,
            author,
            pic, 
            tArea
        });
    }

    //Delete post
    static deletePost(id) {
        return axios.delete(`${url}${id}`);
    }

    //Update post
    static updatePost(id, updateThePost, textAuthor, picture, textArea) {
        return axios.put(`${url}${id}`,{
            text: updateThePost,
            author: textAuthor,
            pic: picture,
            tArea: textArea
        });
    }
}

export default PostService;