import Vue from 'vue';
import Router from 'vue-router';
import Login from '../components/Login';
import Register from '../components/Register';
import Dashboard from '../components/Dashboard';
import firebase from "firebase";

Vue.use(Router)

const router = new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [
        {
        path: '/login',
        name: 'login',
        component: Login,
        meta: {
            requiresGuest: true
        }
    },
    {
        path: '/register',
        name: 'Register',
        component: Register,

    },
    {
        path: '/dashboard',
        name: 'Dashboard',
        component: Dashboard,
        meta: {
            requiresAuth: true
        }
        
    }
]
});

//Nav Guards
router.beforeEach((to, from, next) => {
    //Chek for requiredAuth guard
        if(to.matched.some(record => record.meta.requiresAuth)){
        //Checked if not logged in
        if(!firebase.auth().currentUser) {
            //Go to Login page
            next({
                path:'/login',
                query: {
                    redirect: to.fullPath
                },
            });
        }else {
            //Procede to route
            next();
        }
    }else if(to.matched.some(record => record.meta.requiresGuset)) {
        //Checked if not logged in
        if(!firebase.auth().currentUser) {
            //Go to Login page
            next({
                path:'/dashboard',
                query: {
                    redirect: to.fullPath
                }
            });
        }else {
            //Procede to route
            next();
        }
    }else {
        next();
    }
});

export default router