<template>

  <div class="container">
     <img class="logo" src="../../pictures/logo.png" alt="logo">
    <hr>
    <!--Create post form-->
    <div class="create-post"> 
              <form id="myForm">
                <label for="create-post">Titel:</label><br>
                <input type="text" id="create-post" v-model="text" placeholder=""><br>
                <label for="create-post">Författare:</label><br>
                <input type="text" id="author" v-model="textAuthor" placeholder=""><br>
                <label for="create-post">Inlägg:</label><br>
                <vue-editor name="text" id="textArea" v-model="textArea"></vue-editor>
              </form>

            <div>
              <!--Upload image on firebase-->
              <p>Ladda upp bild:</p>
              <input type="file" @change="previewImage" accept="image/*" >
             </div>

            <div>
              <!--Progress for uploading-->
              <p>Laddar upp... {{uploadValue.toFixed()+"%"}} 
              <progress id="progress" :value="uploadValue" max="100" ></progress>  </p>
            </div>
            <div v-if="imageData!=null">
              <img class="preview" :src="picture">
              <br>
              <button class="buttonUp" @click="onUpload">Ladda upp bild</button>
            </div>
            
            <button class="buttonUp" v-on:click="createPost">Posta inlägg</button>
            <button class="buttonUp" id="EditButton" v-on:click="updatePost(editId, text, textAuthor, textArea)">Updatera post</button>
      </div>

        <!--Output for post-->
        <p class="error" v-if="error">{{ error }}</p>
        <div class="post-container">
          <!--Slice reverse makes latest post first-->
          <div class="post"
            v-for="(post, index ) in posts.slice().reverse()"     
            v-bind:item="post"
            v-bind:index="index"
            v-bind:key="post._id"
          >
            <p class="title">{{ post.text }}</p>
            <div id="imgContainer">
              <img v-bind:src="post.pic"></div>
              <p class="text" v-html="post.tArea"></p>
              <p class="author">Författare: {{ post.author }}</p>
              <p class="time">{{ `${post.createdAt.getFullYear()}-${post.createdAt.getMonth() +1}-${post.createdAt.getDate()}`}}</p>
              <a href="#"><button id="buttonDown1" v-on:click="updateThis(post._id, post.pic, post.text, post.author, post.tArea)">Updatera post</button></a>
              <button id="buttonDown2" v-on:click="deletePost(post._id)">Radera</button>
            </div>
      </div>
  </div>
</template>


<script>
import PostService from '../PostService';
import firebase from 'firebase';
import { VueEditor } from "vue2-editor";

export default {
  name: 'PostComponent',
  components: {
  VueEditor //Vue editor for textarea
  },
  data() { //Data avalible in component
    return {
      posts: [],
      error: '',
      text:'',
      textAuthor: '',
      textArea: '',
      imageData: null,
      picture: null,
      uploadValue: 0,
      //Toggle: true,
      EditBool: false,
      editId: '',
    }
  },
  async created() { //Run when component is created
    try {
      this.posts = await PostService.getPosts();
      this.showEditButton(); //Edit button 
    }catch(err) {
      this.error = err.message;
    }
  },
  methods: { //Method call 
    async createPost() {
      await PostService.insertPost(this.text, this.textAuthor, this.picture, this.textArea);
      this.posts = await PostService.getPosts();
      setTimeout(function () { window.location.reload(); }, 3) //Reload the page and clear input
    },

    async deletePost(id) {
      if (confirm(`Är du säker på att du vill radera inlägget?`)) {
         await PostService.deletePost(id);
         this.posts = await PostService.getPosts();
      }    
    },

    async updatePost(id) {
      this.editId = id;
       await PostService.updatePost(this.editId, this.text, this.textAuthor, this.picture, this.textArea);
       this.posts = await PostService.getPosts();
       setTimeout(function () { window.location.reload(); }, 3) //Reload the page and clear input
    },
    
    showEditButton() {
      if(this.EditBool){
        document.getElementById("EditButton").style.display = "inline-block";
      } else {
        document.getElementById("EditButton").style.display = "none";
      }
    },

    updateThis(id, pic, text, author, tArea) {
      this.text = text
      this.textAuthor = author
      this.textArea = tArea
      this.editId = id
      this.picture = pic
      this.imageData = !null
      this.EditBool = !this.EditBool;
      this.showEditButton()
    },

    previewImage(event) {
      this.uploadValue=0;
      this.picture=null;
      this.imageData = event.target.files[0];
    },

    onUpload(){
      this.picture=null;
      const storageRef=firebase.storage().ref(`${this.imageData.name}`).put(this.imageData);
      storageRef.on(`state_changed`,snapshot=>{
        this.uploadValue = (snapshot.bytesTransferred/snapshot.totalBytes)*100;
      }, error=>{console.log(error.message)},
      ()=>{this.uploadValue=100;
        storageRef.snapshot.ref.getDownloadURL().then((url)=>{
          this.picture =url;
        });
      }
      );
    },

  }
}
</script>

